











import Vue from 'vue';
import {Component,Prop,Watch} from 'vue-property-decorator';
@Component
export default class Notes extends Vue {
  value='';
  @Prop(String) readonly fieldName: string | undefined;
  @Prop() placeholder?: string;
  @Watch('value')
  onNotesChange(){
    this.$emit('update:value',this.value);
  }
}
